import { Injectable } from '@angular/core';

@Injectable()
export class ConfigService {
    _apiURI: string;
    constructor() {
        //this._apiURI = 'http://localhost:5050/api';
        this._apiURI = '/api';
    }
    getApiURI() {
        return this._apiURI;
    }    
}



